export default {
    state: {
        indexClientes: '',
        indexPacientes: '',
    },
    mutations: {
        setIndexClientes (state, env) {
            state.indexClientes = env === 'production' ? 'prod_clientes' : 'dev_clientes'
        },
        setIndexPacientes (state, env) {
            state.indexPacientes = env === 'production' ? 'prod_pacientes' : 'dev_pacientes'
        },
    },
    actions: {
        
    },
    getters: {
        getIndexClientes (state) {
            return state.indexClientes
        },
        getIndexPacientes (state) {
            return state.indexPacientes
        },
    }
}